/*
	Base styling that's 99% not going to change. There should be no
	configuration in this file, just styling. For config see settings.
   ========================================================================= */


/* ==========================================================================
   	Base
   ========================================================================= */

html {
  // Prevent over-scroll bounce effect
  overflow: hidden;
  font-family: $base-font-family;
}

body {
  @extend %absolute-fill;
  // font-size: 62.5%;
}

/* ==========================================================================
   	Typography
   ========================================================================= */
h1, h2, h3,
h4, h5, h6 {
  margin: 0 0 16px;
  font-family: $base-font-family;
  font-weight: $header-font-weight;
  color: $base-text-color;
}
h1 {
  font-size: $inuit-heading-size-1;
}
h2 {
  font-size: $inuit-heading-size-2;
}
h3 {
  font-size: $inuit-heading-size-3;
}
h4 {
  font-size: $inuit-heading-size-4;
}
h5 {
  font-size: $inuit-heading-size-5;
}
h6 {
  font-size: $inuit-heading-size-6;
}


a {
  text-decoration: none;
  color: #4c94b9;
}
