%i--after,
%i--before {
  position: relative;
  vertical-align: middle;
}

%i--before {
  padding-right: $half-spacing-unit;
}
%i--after {
  padding-left: $half-spacing-unit;
}


.i--pencil {
  &:after {
    content: url('/images/icons/pencil.svg');
    display: inline-block;
    padding-left: $half-spacing-unit;
  }
}

.i--chevron {
  &:after {
    content: url('/images/icons/chevron.svg');
  }
}

/* TODO remove? */
.i--right-chevron {
  &:after {
    @extend %i--after;
    content: url('/images/icons/right-chevron.svg');
  }
}

