.i--chevrons {
  &:after {
    @extend %u-v-center;
    position: absolute !important;
    right: 0;
    content: url('/images/icons/chevrons.svg');
    padding-left: $base-spacing-unit;
  }
}

.i--twitter {
  &:before {
    content: url('/images/icons/twitter.svg');
    position: absolute;
    bottom: 0;
    left: $base-spacing-unit;
  }
}

.i--facebook {
  &:before {
    content: url('/images/icons/facebook.svg');
    position: absolute;
    bottom: 1px;
    left: $base-spacing-unit;
  }
}

.i--search-small {
  &:after {
    content: url('/images/icons/search-small.svg');
    position: absolute;
    top: 53%;
    right: $half-spacing-unit;
    transform: translateY(-50%);
  }
}
