/*
	Brand configuration
   ========================================================================== */

$brand-rounding: 2px;

$brand-border: 1px solid #d1d5d7;
$brand-border--dark: 1px solid #535355;

$brand-box-shadow: inset 0px 0px 3px rgba(0, 0, 0, 0.45);
$brand-box-shadow--dark: inset 0px 0px 3px rgba(0, 0, 0, 0.65);

%brand-focus {
  box-shadow: 0px 0px 4px 0px rgba(80,131,219,0.64), inset 0px 1px 1px 0px rgba(0,0,0,0.08);
  outline: none;
}

