.session-admin {
  padding: $base-spacing-unit;
}

  .session-admin--page-title {
    font-size: $inuit-base-font-size * 2;
  }

  .session-admin--page-container {
    background-color: #fff;
    border-radius: $brand-rounding;
    border: solid 1px;
    border-color: #ccc;
    float: left;
    width: 100%;
  }

// this will be pulled out when navigation is
// done
.session-admin-navigation {
  padding-left: 30px;
  border-right: solid 1px #ccc;
  width: 150px;
  float: left;
}

.session-admin-navigation__heading {
  color: #aaa;
  padding-top: $large-spacing-unit;
  text-transform: uppercase;
}
.session-admin-navigation__list-group {
  list-style: none;
}
