/*
	Nav module
   ========================================================================== */

$primary-nav-height:            42px;
$secondary-nav-height:          42px;
$total-nav-height:              $primary-nav-height + $secondary-nav-height;
$nav-line-height:               37px;


%nav-layout {
  height: 100%;
  width: 100%;
  line-height: $nav-line-height;
}
%nav-item-layout {
  height: 100%;
  display: inline-block;
  padding: 0 $half-spacing-unit;
  border-top: 2px solid transparent;
  cursor: pointer;
}
%nav-is-active {
  background-color: #000000;
  border-top: 2px solid #5083DB;
}


.nav {
  @extend %nav-layout;
  height: $primary-nav-height;
  width: 100%;
  position: relative;
  background-color: #253350;
}

.nav__items {
  height: 100%;
  width: 100%;
}

.nav__item {
  @extend %nav-item-layout;
  color: #b3b6ba;
  text-transform: uppercase;
  text-decoration: none;

  &.is-active {
    @extend %nav-is-active;
  }
}

.nav__image {
  height: $primary-nav-height;
  width: $primary-nav-height;
  display: inline-block;
  vertical-align: top;
  background: url('/images/brand/kayako-logo.svg') no-repeat;
  background-size: cover;
}

//Main Nav
.nav-main {
  @extend %nav-layout;
}

.nav-main__item {
  @extend %nav-item-layout;
  color: #b3b6ba;
  text-transform: uppercase;
  font-weight: 600;

  &.is-active {
    @extend %nav-is-active;
  }
}


//nav secondary
.nav-secondary {
  background-color: #ffffff;
  border-bottom: $brand-border;
  height: $secondary-nav-height;
}


//nav tabs
.nav-tabs {
  @extend %nav-layout;
  border-left: $brand-border--dark;
}

.nav-tabs__item {
  @extend %nav-item-layout;
  float: left;
  position: relative;
  padding-right: $base-spacing-unit + $quarter-spacing-unit;
  border-right: $brand-border--dark;
  color: #b3b6ba;

  &.is-active {
    @extend %nav-is-active;
  }

  &:last-of-type {
    border-right: none;
  }
}

.nav-tabs__close {
  position: absolute;
  top: 50%;
  transform: translateY(-52%);
  right: 8px;
  cursor: pointer;
}


//nav-new
.nav-new {
  @extend %nav-item-layout;
  @extend %u-v-center;
  height: 20px;
  width: 20px;
  margin: 0 0 0 $half-spacing-unit;
  line-height: initial;
  color: #FFFFFF;
  font-weight: 700;
  background-color: #5083DB;
  border-radius: $brand-rounding;

  &:after {
    content: '+';
    position: absolute;
    top: -3.5px;
    right: 0.5px;
    left: 0;
    margin: 0;
    font-size: $base-spacing-unit;
    text-align: center;
  }
}

