.dropdown {
  width: 100%;
  position: relative;
  display: block;
  padding-right: $half-spacing-unit;
  color: #3B4048;
}

.dropdown-menu {
  @extend %list-bare;
  z-index: 2;
  position: absolute;
  right: 0;
  left: 0;
  padding: $quarter-spacing-unit 0 0;
  cursor: pointer;
  background: #FFFFFF;
  border-bottom: 1px solid #D1D5D7;

  &:focus {
    outline: none;
  }
}

.dropdown-menu__item {
  padding: $quarter-spacing-unit $base-spacing-unit;
  border: 1px solid transparent;

  &:focus,
  &:hover {
    background: #5083DB;
    border: 1px solid #3968B8;
    color: #FFFFFF;

    & > * {
      color: #FFFFFF;
    }
  }

  &:focus {
    box-shadow: 0px 0px 4px 0px rgba(80,131,219,0.64), inset 0px 1px 1px 0px rgba(0,0,0,0.08);
    outline: none;
  }
}
