/*
	Base input styling and initial configuration. Types of input should be
	separated out into their own file (text, text-area etc).
   ========================================================================== */

$input-text-border: 1px solid $color-trim;
$input-base-width: 285px;


%input-style {
  padding: 6px 10px;
  margin-bottom: $half-spacing-unit;
  border: $input-text-border;
  border-radius: $brand-rounding;
  box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.31);

    &:last-of-type {
      margin-bottom: 0;
    }

    &.error {
      border: 1px solid red;
    }

    &:focus {
      outline: none;
    }

    &:disabled {
      background-color: #eeeeee;
      opacity: 0.8;
    }

    &.has-icon {
      padding-right: $base-spacing-unit + $half-spacing-unit;
    }
}


//A grey counter positioned at the bottom right of (usually) a text area
//To display how many more characters the user can type.
.input__counter {
  @extend %t-caption;

  position: absolute;
  right: $base-spacing-unit;
  bottom: $base-spacing-unit;
}
