/*
	Profile module
   ========================================================================== */

$profile-image-size: 20px;


.profile {
  width: 100%;
  display: inline-block;
  padding: 0 $half-spacing-unit;
  vertical-align: baseline;
}

.profile__image {
  height: $profile-image-size;
  width: $profile-image-size;
  margin-right: $half-spacing-unit;
  vertical-align: bottom;
}
