/* ==========================================================================
   	Displays
   ========================================================================= */
.u-inline-block {
  display: inline-block;
  height: 100%;
  vertical-align: top;
}

.u-hidden {
  display: none !important;
}

.u-visible {
  visibility: visible !important;
}

.u-disable-link {
	pointer-events: none;
}

.u-no-bottom-border {
  border-bottom: none;
}

/* ==========================================================================
   	Positioning
   ========================================================================= */

.u-pos-rel {
  position: relative;
}

.u-post-abs {
  position: absolute;
}


%u-v-center,
.u-v-center {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}


%u-h-center,
.u-h-center {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}


%u-center,
.u-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

%u-center-parent {
  transform-style: preserve-3d;
}

/**
 * Absolutely positions and fills the container
 */
%absolute-fill {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}



/*
 * Hacky way of getting sensual vertical alignment
 * Used to align the nav-new button mostly.
 *
 * [1] Closest fall back for middle alignment
 * [2] Align in modern browsers perfectly, yay.
 */
.u-v-align-fix {
  vertical-align: middle; /*[1]*/
  vertical-align: -webkit-baseline-middle; /*[2]*/
}


.u-overflow-scroll {
  overflow: scroll;
}
