.styleguide {
  padding: $base-spacing-unit;
  background-color: #FFFFFF;

  & code {
    display: block;
    padding: 0 18px;
    margin: 18px 0px 18px;
    border: 1px solid #000000;
    background-color: #F8F8F8;
    font-family: Consolas, Monospace;
    white-space: pre;
  }
}

.styleguide__item {
  margin: $large-spacing-unit 0;
}
