/*
	Button styling
   ========================================================================= */

%button,
.button {
  display: inline-block;
  position: relative;
  padding: $quarter-spacing-unit $base-spacing-unit;
  background: none;
  border-radius: $brand-rounding;
  outline: none;
  border: none;
  cursor: pointer;
  font-weight: 600;

    &:disabled,
    &.disabled {
      opacity: 0.6;
      pointer-events: none;
    }
}

  .button--default {
    background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, #FAFAFA 100%);
    box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.05);
    border: $brand-border;
    border-radius: 2px;
    color: #5F6C73;

      &:hover {
        background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, #F6F6F6 100%);
        border: 1px solid #ACB9BF;
        color: #2D3138;
      }
  }

  .button--action {
    @extend .button--default;
    padding-right: $half-spacing-unit;
  }

  .button--primary {
    background-image: linear-gradient(-180deg, #4F82DC 4%, #4278D3 100%);
    border: 1px solid #3464B6;
    box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.09), inset 0px 1px 0px 0px #588EEC;
    color: #FFFFFF;

      &:hover {
        background-image: linear-gradient(-180deg, #4475CC 22%, #376ABF 100%);
        box-shadow: inset 0px 1px 0px 0px #4D82DB;
        color: #FFFFFF;
      }
  }

  .button--highlight {
    background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, #FAFAFA 100%);
    border: 1px solid #5083DB;
    box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.05);
    color: #5083DB;

      &:hover {
        background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, #FAFAFA 100%);
        border: 1px solid #3968B8;
        color: #3968B8;
      }
  }

  .button--alert {
    background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, #FAFAFA 100%);
    border: 1px solid #E86048;
    box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.05);
    color: #E86048;

      &:hover {
        background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, #FAFAFA 100%);
        border: 1px solid #CA4A33;
        color: #CA4A33;
      }
  }

  .button--twitter {
    padding-left: ($base-spacing-unit * 2) + $quarter-spacing-unit;
    background-image: linear-gradient(-180deg, #55ACEE 0%, #51A5E3 100%);
    border: 1px solid #3B88C3;
    box-shadow: 0px 1px 1px 0px #72B9EE, inset 0px 1px 0px 0px rgba(0,0,0,0.14);
    color: #FFFFFF;

      &:hover {
        background-image: linear-gradient(-180deg, darken(#55ACEE, 5%) 0%, darken(#51A5E3, 5%) 100%);
        border: 1px solid darken(#3B88C3, 5%);
      }
  }

  .button--facebook {
    padding-left: ($base-spacing-unit * 2);
    background-image: linear-gradient(-180deg, #4F6AA3 0%, #3A5999 100%);
    border: 1px solid #1D326D;
    box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.09), inset 0px 1px 0px 0px #4A6AAD;
    color: #FFFFFF;

      &:hover {
        background-image: linear-gradient(-180deg, darken(#4F6AA3, 5%) 0%, darken(#3A5999, 5%) 100%);
        border: 1px solid darken(#1D326D, 5%);
      }
  }

.button-split {
  display: inline-block;

    & > * {
      display: inline-block;
    }

    & .button {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      vertical-align: top;
    }
}
  .button-split__action {
    @extend %button;

    position: relative;
    padding-left: $half-spacing-unit;
    padding-right: $half-spacing-unit;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;

      &:hover {
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
      }
  }



.button-group {

}
  .button-group__item {
    @extend %button;
    @extend .button--default;
    border-right: none;
    border-radius: 0;

      &.is-active {
        background-image: linear-gradient(-179deg, rgba(245,246,247,0.00) 0%, #F2F4F5 100%);
        color: #2D3138;
      }

      &:hover {
        border: $brand-border; //reset the borders inherited from --default.
        border-right: none;
      }

      &:first-of-type {
        border-top-left-radius: $brand-rounding;
        border-bottom-left-radius: $brand-rounding;
      }
      &:last-of-type {
        border-right: $brand-border;
        border-top-right-radius: $brand-rounding;
        border-bottom-right-radius: $brand-rounding;
      }
  }




