/*
	Arrow object
   ========================================================================== */

/**
* Standalone arrow that only needs to be contained within the same container that is position: relative;
* It does not use a pseudo selector for styling as we needed to control the positioning of the arrow via javascript
* so the context-modal can be dynamically positioned. This cannot be achieved with pseudo selectors.
* May be useful to be turned into mixin.
*/
$arrow-size: 9px;
$arrow-border: 1px solid #BEC4C7;

.arrow {
  width: $arrow-size;
  height: $arrow-size;
  position: absolute;
  left: calc(50% - #{$arrow-size});
  pointer-events: none;
  transform: rotate(45deg);

  &--top {
    top: floor(-$arrow-size / 2);
    background: #FFFFFF;
    border-top: $arrow-border;
    border-left: $arrow-border;
  }

  &--bottom {
    bottom: floor(-$arrow-size / 2);
    background: #F7F8F9;
    border-bottom: $arrow-border;
    border-right: $arrow-border;
  }
}
