.figure-inline {

    & > * {
      display: inline-block;
      vertical-align: middle;
    }
}
  %figure-inline__image,
  .figure-inline__image {
    width: 42px;
    height: 42px;
    margin-right: 8px;
    border-radius: $brand-rounding;

      &:last-child {
        margin-right: 0;
      }
  }

  .figure-inline__image--med {
    @extend %figure-inline__image;

    width: 30px;
    height: 30px;
  }

  .figure-inline__image--small {
    @extend %figure-inline__image;

    width: 24px;
    height: 24px;
  }

  .figure-inline__caption {
    vertical-align: -webkit-baseline-middle;
  }


.avatar {
  border-radius: $brand-rounding;
  height: 30px;
  width: 30px;
}


