.content-tabs-group {
  @extend %list-inline;

  & .content-tabs {
    border-right: 1px solid $color-trim;

    &:last-of-type {
      border-right: none;
    }
  }

  & .content-tabs__item {
    margin: 0 16px;
  }
}


.content-tabs {
  @extend %list-inline;
  border-bottom: 1px solid $color-trim;
  vertical-align: middle;
}

%content-tabs__item,
.content-tabs__item {
  padding: 16px 0;
  margin: 0 16px 0 0;
  border-bottom: 2px solid transparent;

  &.active {
    border-bottom: 2px solid $color-primary;
    font-weight: 600;
  }
}

.content-tabs__action {
  @extend %content-tabs__item;
  float: right;
  margin: 0;
}
