/*
	Table styling
   ========================================================================== */

%table-cell {
  padding: 8px 0;
}

%table,
.table {
  width: 100%;
  height: 100%;
  border: none;
}
  .table__row {
    border-top: 1px solid #ebedee;
    border-bottom: 1px solid #ebedee;
  }
  %table__data,
  .table__data {
    @extend %table-cell;
  }
  %table__header,
  .table__header {
    @extend %table-cell;
    font-weight: 600;
    text-transform: uppercase;
    text-align: left;
    color: $color-text-secondary--dark;
  }


.table--case {
  width: calc(100% - #{$large-spacing-unit});
  margin: 0 auto;
  color: $color-text-secondary--dark;
}
  .table__data--case {
    @extend %table-cell;
    font-weight: 600;
    color: $color-text-primary;
  }
