.ko-session-cases-index {
  @extend %absolute-fill;

  &__scrollable {
    @extend %absolute-fill;
    overflow: auto;
  }

  &__contents {
    @extend .container;
    background: #FFFFFF;
    border: 1px solid #D1D5D7;
    border-radius: 4px;
    padding: 0 17px;
  }

  &__pagination {
    width: 44px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
  }
}
