}.ko-toggle-87eebd30{/**
 * [1] - we are using div instead of input element hence the styling.
 */
$ko-toggle-border-radius: 31px;

.ko-toggle__container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 66px;
  height: 30px;
  background: #BEC4C7; /*[1]*/
  box-shadow: inset 0px 1px 3px 0px rgba(0,0,0,0.09); /*[1]*/
  border-radius: $ko-toggle-border-radius; /*[1]*/
  transition: background 300ms; /*[1]*/

    &--activated {
      background: #85A043;
    }

    &--micro {
      width: 35px;
      height: 18px;
    }

  .ko-toggle__toggle {
    width: 24px;
    height: 24px;
    margin-left: 3px;
    background: #FFFFFF; /*[1]*/
    box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.24), 0px 1px 1px 0px rgba(0,0,0,0.05); /*[1]*/
    border-radius: $ko-toggle-border-radius; /*[1]*/
    transition: margin 300ms; /*[1]*/

      &--activated {
        margin-left: 39px;
      }

      &--micro {
        width: 12px;
        height: 12px;
      }
  }
  .ko-toggle__toggle--activated.ko-toggle__toggle--micro {
    margin-left: 20px;
  }
}

.ko-toggle__label {
  @include rem('font-size', '12px');
  position: relative;
  color: #838D94;
  margin-left: $half-spacing-unit;

    &--micro {
      margin-left: $quarter-spacing-unit;
    }
}

}.login-19f8cf83{/*
  Login box that appears when an unauthorised user hits /admin
  Has two states: Fresh login (username/password) or a remembered login
  that shows the users gravatar (login__image) and only a password field.
   ========================================================================= */


$fieldHeight: 74px;

.login {
  width: 385px;
  position: relative;
  margin: 165px auto $half-spacing-unit;
  border: 1px solid $color-trim;
  border-radius: $brand-rounding;
  background-color: #ffffff;

    @include media-query('palm') {
      width: 90%;
      margin-left: auto;
      margin-right: auto;
    }
}

  .login__header {
    margin-bottom: $base-spacing-unit;
    font-weight: 400;
  }
    .login__header--reset {
      padding-bottom: 42px;
    }

  .login__image {
    width: 100px;
    height: 100px;
    padding: $half-spacing-unit;
    background-size: cover;
    border-radius: $brand-rounding;
  }


  .login__input {
    @extend %input-style;
    width: 100%;
  }

  .login__actions {
    min-height: 40px;
    line-height: 40px;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    border-top: 1px solid $color-trim;
  }


.login-form {
  padding: 67px 0 0;
  overflow: hidden;
}

  .login-form__mask {
    position: relative;
    overflow: hidden;
    height: $fieldHeight;
    width: 100%;
  }

    .login-form__content {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: $fieldHeight * 2;
    }

    .login-form__content--animate {
      transition: transform 0.3s ease-in-out;
    }

    .login-form__content-down {
      transform: translate(0,-$fieldHeight);
    }

      .login-form__fields-container-top {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: $fieldHeight;
      }

      .login-form__fields-container-bottom {
        position: absolute;
        top: $fieldHeight;
        left: 0;
        right: 0;
        height: $fieldHeight;
      }



  .login-form__wrapper {
    padding: 0 $base-spacing-unit;
  }

  .login-form__container {
    width: 200%;
    height: 100%;
    display: table;
    table-layout: fixed;
    transition: transform 0.35s ease-in-out;

      &.u-slide {
        transform: translate(-50%, 0);
        transition: transform 0.35s ease-in-out;
      }
  }

  .login-form__form,
  .login-form__reset-form {
    width: 100%;
    position: relative;
    display: table-cell;
    padding-bottom: 63px;
    -webkit-font-smoothing: subpixel-antialiased;
  }

  .login-form__reset-form {
    vertical-align: bottom;
  }


