.a-error {
  animation: a-error 1s ease-in-out 0s alternate forwards;
}

.a-success {
  animation: a-success 1s ease-in-out 0s infinite alternate;
}

@keyframes a-success{
  0%   { background-color: transparent; }
  100% { background-color: #88a932; }
}
@keyframes a-error {
  0%   { background-color: transparent; }
  100% { background-color: #e95f41; }
}


.a-fadeInOut,
%a-fadeInOut {
  animation: a-fadeInOut 1s ease-in-out 0s infinite;
}

@keyframes a-fadeInOut {
  0%   {opacity: .3;}
  50%  {opacity: .9;}
  100%  {opacity: .3;}
}

