/*
	Box object
   ========================================================================== */

/**
* Generic box that can be extended upon, ensures spacing is consistent throughout the site.
*/
.box {
  padding: $half-spacing-unit;
  background-color: $color-secondary;
  border-bottom: 1px solid $color-trim;

  &:last-of-type {
    border-bottom: none;
  }

  &--secondary {
    background-color: #ffffff;
  }

  &--alone { // stupid name, rename. @todo
    border-top: 1px solid $color-trim;
    border-bottom: 1px solid $color-trim;
  }

  &--wide {
    padding: $half-spacing-unit 0;
  }
}

/**
 * For when it does not have a parent container
 * that brings its own border style
 */
.box-container {
  border: 1px solid $color-trim;
}
