/*
	Text area styling
   ========================================================================== */

.input__text-area {
  @extend %input-style;
  width: $input-base-width;
  margin-bottom: $half-spacing-unit;
}

.input__text-area--clean {
  @extend %input-style;
  width: 100%;
  min-height: 147px;
  padding: $base-spacing-unit ($large-spacing-unit + $half-spacing-unit) $base-spacing-unit $base-spacing-unit;
  margin-bottom: $half-spacing-unit;
  resize: none;
  border: none;
  border-bottom: 1px solid $color-trim;
  border-radius: 0;
}
