/*
	Default configuration
   ========================================================================== */

$inuit-base-font-size:          14px;
$inuit-base-line-height:        18px;
$inuit-base-text-color:         #3B4048;
$inuit-base-background-color:   #f8f8f8;


$inuit-namespace: null;


$inuit-base-spacing-unit:           $inuit-base-line-height;
$inuit-base-spacing-unit--small:    $inuit-base-spacing-unit / 2;
$inuit-base-spacing-unit--large:    $inuit-base-spacing-unit * 2;



//Alias the annoying inuit variables
$base-spacing-unit:       $inuit-base-spacing-unit;
$half-spacing-unit:       $inuit-base-spacing-unit--small;
$quarter-spacing-unit:    $inuit-base-spacing-unit--small / 2;
$large-spacing-unit:      $inuit-base-spacing-unit--large;

$base-font-size:          $inuit-base-font-size;
$base-line-height:        $inuit-base-line-height;
$base-text-color:         $inuit-base-text-color;
$base-background-color:   $inuit-base-background-color;
$base-font-family:        'Source Sans Pro', Sans-Serif;
