.u-typing {
  width: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
  .u-typing__item {
    position: absolute;
    width: 4px;
    height: 4px;
    margin: 0;
    padding: 0;
    background-color: #fff;
    @extend %a-fadeInOut;
    border-radius: 50%;

      &:nth-child(2) {
        left: 6px;
        animation-timing-function: ease-in-out;
        animation-delay: .1s;
      }

      &:nth-child(3) {
        left: 12px;
        animation-delay: .2s;
      }
  }


.u-is-typing {

}

.u-was-typing {

    & > * {
      background-color: #838D94;
    }
}

.u-is-inactive {
  opacity: 0.5;
}
