.flip-container.flip .flipper {
  transform: rotateY(180deg);
}
.flip-container, .front, .back {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  position: absolute;
  top: -60px;
  left: 0;
  right: 0;
}
.flip-container {
  perspective: 1000;
  -webkit-perspective: 1000;
  width: 115px;
  height: 115px;
  padding: 7px;
  border-radius: $brand-rounding;
  background-color: transparent;
  transition: background-color 0.85s ease-in-out;
}
.flipper {
  transition: 0.7s;
  transform-style: preserve-3d;
  position: relative;
}
.front, .back {
  -webkit-backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}
.front {
  z-index: 2;
}
.back {
  transform: rotateY(180deg);
}
