/*
	Typography utility classes
   ========================================================================= */

$typographyStates: (
  good: $color-good,
  bad: $color-bad,
  warning: $color-warning
);

$typographyPositions: center, right, left;


// We must create a map of the state/var as variable interpolation
// isn't as robust as we need it to be (e.g: color: $color-#{$state}) as
// Sass doesn't have dynamic variables!
@each $state, $var in $typographyStates {
  %t-#{$state},
  .t-#{$state} {
    color: $var;
  }
}

// Generates text align position classes based off the passed in values
@each $position in $typographyPositions {
  %t-#{$position},
  .t-#{$position} {
    text-align: $position;
  }
}


.t-small {
  font-size: $base-font-size - 2px;
}
