.is-highlighted {
  //We move back 1px, then overflow the width by 2px to offset the move + the 1px border.
  width: calc(100% + 2px);// Sorry. But to get the border to overlap its container this is necessary.
  margin-left: -1px;
  background-color: #5083DB;
  border: 1px solid #3968B8;
  color:  #ffffff;
}

.partial-highlight {
  color: #3B4048;
  font-weight: 700;
}
