/*
	Info bar module
   ========================================================================== */

.info-bar-item {
  position: relative;
  border-bottom: 1px solid #D1D5D7;
  @include rem(padding, $half-spacing-unit $base-spacing-unit);

  &:hover {
    background-color: #FFFFFF;
  }

  &:focus {
    outline: none;
  }

  &:last-of-type {
    border-bottom: none;
  }

  &--edited {
    border: 1px solid #5083DB !important;
    border-left: 5px solid #5083DB !important;
    margin: -1px -1px 0 -1px;
    padding: $half-spacing-unit $base-spacing-unit $half-spacing-unit calc($base-spacing-unit - 4px);
  }

  &--error {
    border: 1px solid #E86048 !important;
    border-left: 5px solid #E86048 !important;
    margin: -1px -1px 0 -1px;
    padding: $half-spacing-unit $base-spacing-unit $half-spacing-unit calc($base-spacing-unit - 4px);
  }
}

.info-bar-item__header {
  @extend %t-caption;
  @include rem(font-size, 12px);
}

.info-bar-item__content {
  padding: $quarter-spacing-unit 0 0 0;
}
