/*
    <input type="text" /> styling, although it should be used as the base
    for all text (email/textbox etc) input unless they require very specific
    styling, in which case a new file should be made.
   ========================================================================= */


/* ==========================================================================
   	Config
   ========================================================================= */


/* ==========================================================================
   ========================================================================= */

.input {
  position: relative;

    & > * {
      display: block;
    }
}

.input__text {
  @extend %input-style;

  width: $input-base-width;
}

/* ==========================================================================
      In constrast to the usual styling, sometimes inputs are on their own.
      This overrides the removal of top borders. As it is the first and last
      type, border radius will display properly anyway.
     ========================================================================= */

.input__text--alone {
  @extend %input-style;

    &:last-of-type, &:first-of-type {
      border: $input-text-border;
    }
    &:nth-child(even) {
      border: $input-text-border;
    }
}

.input__text--full {
  @extend %input-style;

  width: 100%;
}


/**
 * Styling for the html5 placeholder="attribute"
 */
::-webkit-input-placeholder {
  font-weight: 300;
  color: $color-text-primary;
  opacity: 0.6;
}


.input-stacked {

    & > .input__text {

        &:first-of-type {
          border-bottom: 0;
          border-top-right-radius: $brand-rounding;
          border-top-left-radius: $brand-rounding;
        }

        &:last-of-type {
          margin-bottom: 24px;
          border-bottom-right-radius: $brand-rounding;
          border-bottom-left-radius: $brand-rounding;
        }
    }
}
