/*
	Custom heading styles, please try and avoid setting font sizes here,
	unless there's an extremely specific use case for it.
	======================================================================== */

/*
	Enforces a block hierarchy for headings/subheading these should be treated
	like utility classes more than styling.
*/
%heading,
.heading {
  display: block;
  margin-bottom: 0;

  &--alone {
    font-weight: 600;
  }
}

.subheading {
  display: block;
  margin: 2px 0 24px;
}

